import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { ApiNewsletterService } from '../../api-services/newsletter.service';

@Component({
  selector: 'app-footer-one',
  templateUrl: './footer-one.component.html',
  styleUrls: ['./footer-one.component.scss']
})
export class FooterOneComponent implements OnInit {

  @Input() class: string = 'footer-light' // Default class 
  @Input() themeLogo: string = 'web/assets/images/icon/logo.png' // Default Logo
  @Input() newsletter: boolean = true; // Default True

  public today: number = Date.now();
  public email: string = '';
  public showThanksForNewsletter: boolean = false;

  constructor(
    private router: Router,
    private apiNewsletterService: ApiNewsletterService
  ) { }

  ngOnInit(): void {
  }

  onAddNewsletterContact() {
    this.apiNewsletterService.addContact(this.email).subscribe(
      (response) => {
        this.showThanksForNewsletter = true;
      }
    );
  }

  onGoToHome() {
    this.router.navigate(['/']);
  }

}
