import { Component, OnInit, Input } from '@angular/core';
import { ApiNewsletterService } from '../../api-services/newsletter.service';

@Component({
  selector: 'app-footer-two',
  templateUrl: './footer-two.component.html',
  styleUrls: ['./footer-two.component.scss']
})
export class FooterTwoComponent implements OnInit {

  @Input() class: string;
  @Input() themeLogo: string = 'web/assets/images/icon/logo.png'; // default Logo
  @Input() mainFooter: boolean = true; // Default true 
  @Input() subFooter: boolean = false; // Default false 
  
  public today: number = Date.now();
  public email: string = '';
  public showThanksForNewsletter: boolean = false;
  
  constructor(private apiNewsletterService: ApiNewsletterService) { }

  ngOnInit(): void {
  }

  onAddNewsletterContact() {
    this.apiNewsletterService.addContact(this.email).subscribe(
      (response) => {
        console.log('response')
        console.log(response)
        this.showThanksForNewsletter = true;
      }
    );
  }

}
